import { Component } from '@angular/core';
import {AlertController, IonicPage,ModalController, NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-permiso-list',
  templateUrl: 'permiso-list.html',
})
export class PermisoListPage {

  public loading: boolean = true;
  items: any=[];
  items2: any=[];
    generated = '';
 
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";
  idrol: any;
  nidrol: any;
  idempresa:any;
  pingreso:boolean=false;
  pconsulta:boolean=false;
  peliminacion:boolean=false;
  pcreacion:boolean=false;
  pactualizacion:boolean=false;
 permisom: any=[];
 idafiliado:any='na';
//private barcodeScanner:BarcodeScanner,
  constructor(  public alertCtrl: AlertController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService
    ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth
 

    ) {

     
  }
  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }
  showFilter()     
  {}


  pideregistros() { 
      let self = this;    
      self.loading = true;
      this.regService.getReg(this.idrol +'/' + this.idempresa + '/todos','permisos').then((data) => {
        this.items=data;
        self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
   // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })  
  });
  }
  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }
  ionViewDidLoad() {
    console.log(this.navParams.get("permiso"));
    this.idrol=this.navParams.data.record._id;
    this.nidrol=this.navParams.data.record.nombre;

    this.permisom = String(this.navParams.get("permiso")).split(",");
    this.pingreso=(  this.permisom[0] == 'true');
    this.pconsulta=(  this.permisom[1] == 'true');
    this.peliminacion=(  this.permisom[2] == 'true');
    this.pcreacion=(  this.permisom[3] == 'true');
    this.pactualizacion=(  this.permisom[4] == 'true');
    console.log(this.navParams.get("permiso"));
  
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
      this.pideregistros();
     
  })

   
  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }

  moduloRecord(item : any) : void
  {
  
     this.navCtrl.push('Permiso2ListPage', { record : item ,permiso:this.permisom,record2:{idrol:this.idrol,idpermiso:item._id,npermiso:item.nombre}} );
  }

  deleteRecord(item : any, no) : void
  {
    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operaciÃ³n?',
    buttons: [
      {text: 'Si',
        handler: () => {
              
      
    let recordID 		: string		= item._id;
    this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'permisos').then((data) => {
        this.items.splice(no, 1);
        this.items = this.items.concat([]);
      }, (err) => {
        this.msj.showError3('Error',err.error);
    });
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();
  
  
  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('PermisoCreatePage', { record : item, actualizacion:this.pactualizacion ,record2:{idrol:this.idrol,nidrol:this.nidrol}},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros()

      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('PermisoCreatePage',{ record : null, actualizacion:this.pcreacion ,record2:{idrol:this.idrol,nidrol:this.nidrol}},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
       // this.items = this.items.concat([data]);
       this.pideregistros()

      }  
    })
    addModal.present();

      //this.navCtrl.push('EmpresaCreatePage');
   }
 



  
}



